let strings = {};

fetch('strings.json')
.then(body => body.json())
.then(body => strings = body)
.catch(err => console.error(err));

export const ltrLanguages = ['English', 'Turkish', 'French'];

export function tr(string) {
  if (strings.ar[string]) {
    return strings.ar[string];
  } else {
    if (!string.match(/[ا-ي]/)) {
      console.warn(`translate: "${string}`);
    }
    return string;
  }
}

export function stringOrArray(obj, parent, child, translate = true) {
  if (obj[parent] == null || obj[parent][child] == null){
    return '';
  } else if (Array.isArray(obj[parent][child])) {
    return translate ? obj[parent][child].map(x => tr(x)).join('، ') : obj[parent][child].join('، ');
  } else {
    return translate ? tr(obj[parent][child]) : obj[parent][child];
  }
}

export function languageIcon(lang) {
  const icons = {
    Arabic: '🇸🇾',
    English: '🇬🇧',
    Turkish: '🇹🇷',
    French: '🇫🇷',
    Hebrew: '🇮🇱',
    Chinese: '🇨🇳',
    Farsi: '🇮🇷'
  }

  return Array.isArray(lang)
    ? lang.map(x => icons[x]).join('|')
    : icons[lang];
}
