<script>
export let show = false;

window.onscroll = function(event) {
  show = (document.documentElement.scrollTop || document.body.scrollTop) >= 575 ? true : false;
};
</script>

{#if show}
<div class="top-scrollerx js-top-scrollerx svg-icons" on:click={() => window.scrollTo({
  top: 0,
  behavior: 'smooth'
})}>
   <div><i class="fa fa-chevron-up" aria-hidden="true"></i></div>
</div>
{/if}


