<script>
  import { afterUpdate } from 'svelte';
  import Card from './components/Card.svelte';
  import Navbar from './components/Navbar.svelte';
  import BackToTop from './components/BackToTop.svelte';
  import { tr, languageIcon, stringOrArray } from './functions.js'

	export let entries = [],
  borrowers = [],
  current = null,
  originalEntries = [],
  filteredOriginalEntries = [],
  sortingType = 'titles',
  sortingIndicator = {
    titles: '▼',
    authors: '',
    languages: '',
    ratings: '',
    pub_years: '',
    pages: '',
    genres: '',
  },
  sortingModifier = 'asc';

  const unsplashAccessKey = '0q6izAP15COmmL3gtAzq3gjyrASbnFP_ESnAUbrrBi4';

  fetch('./data.json')
  .then(body => body.json())
  .then(body => {
    originalEntries = body.entry.sort((a, b) => a.title > b.title ? 1 : -1);
    if (body.borrowers) {
      borrowers = typeof(body.borrowers) === 'object' ? [body.borrowers.borrower] : body.borrowers.borrower;
    }
  })
  .then(() => {
    entries = originalEntries;
  })
  .catch(err => console.error(err));

  afterUpdate(() => [].slice.call(document.querySelectorAll('[data-bs-toggle="tooltip"]')).map(tooltipTriggerEl => new bootstrap.Tooltip(tooltipTriggerEl)));

  function showEntryCard(entry) {
    current = entry;
    new bootstrap.Modal(document.getElementById('modal')).toggle();
  }

  function sort(type) {
    sortingModifier = sortingType === type ? (
      sortingModifier === 'asc' ? 'desc' : 'asc'
    ) : 'asc';

    sortingType = type;
    sortingIndicator = {
      titles: '',
      authors: '',
      languages: '',
      ratings: '',
      pub_years: '',
      pages: '',
      genres: '',
    }

    function op(a, b) {
      return sortingModifier === 'asc' ? a > b : a < b;
    }

    function srt(fn) {
      let arr = filteredOriginalEntries.length ? filteredOriginalEntries : originalEntries;
      arr = arr.sort(fn);
      entries = arr.slice(0, entries.length);
    }

    function sortObject(a, b, factor) {
      const pluralFactor = `${factor}s`
      let af = a[pluralFactor] != null ? a[pluralFactor][factor] : ''
      let bf = b[pluralFactor] != null ? b[pluralFactor][factor] : ''
      return op(af, bf) ? 1 : -1
    }

    switch (sortingType) {
      case 'titles':
        srt((a, b) => a.title && b.title && op(a.title, b.title) ? 1 : -1);
        break;
      case 'authors':
        srt((a, b) => sortObject(a, b, 'author'));;
        break;
      case 'languages':
        srt((a, b) => sortObject(a, b, 'language'));
        break;
      case 'ratings':
        // FIXME
        srt((a, b) => a.rating && b.rating && op(parseInt(a.rating), parseInt(b.rating)) ? 1 : -1);
        break;
      case 'pub_years':
        srt((a, b) => a.pub_year && b.pub_year && op(parseInt(a.pub_year), parseInt(b.pub_year)) ? 1 : -1);
        break;
      case 'pages':
        srt((a, b) => a.pages && b.pages && op(parseInt(a.pages), parseInt(b.pages)) ? 1 : -1);
        break;
      case 'genres':
        srt((a, b) => sortObject(a, b, 'genre'));
        break;

      default:
        break;
    }

    sortingIndicator[sortingType] = sortingModifier === 'asc' ? '▼' : '▲';
  }

  function filter(text) {
    function escapeRegExp(string) {
      return string.replace(/[.*+?^${}()|[\]\\]/g, '\\$&'); // $& means the whole matched string
    }

    text = escapeRegExp(text);

    function languageName (lang, text) {
      const names = {
        // TODO : add native names
        Arabic: ["عربي", "عربى"],
        English: ["انجليزي", "انجليزى", "انكليزي", "انكليزى", "انگليزي", "انگليزى", "إنجليزي", "إنجليزى", "إنكليزي", "إنكليزى", "إنگليزي", "إنگليزى"],
        Hebrew: ["عبري", "عبرى"],
        Chinese: ["صيني", "صينى"],
        French: ["فرنسي", "فرنسى"],
        Farsi: ["فارسي", "فارسى"],
        Turkish: ["تركي", "تركى"],
      }
      if (lang.match(new RegExp(text, 'i'))) {
        return true;
      } else {
        return names[lang] ? names[lang].join("").includes(text) : false;
      }
    }

    filteredOriginalEntries = originalEntries
      .filter(x => 
        x.title.match(new RegExp(text, 'i')) ||
        stringOrArray(x, 'authors', 'author', false).match(new RegExp(text, 'i')) ||
        languageIcon(x.languages.language).match(new RegExp(text, 'i')) ||
        languageName(typeof(x.languages.language) === 'object' ? x.languages.language[0] : x.languages.language, text) ||
        stringOrArray(x, 'genres', 'genre').match(new RegExp(text, 'i')) ||
        stringOrArray(x, 'keywords', 'keyword').match(new RegExp(text, 'i'))
      )
    entries = filteredOriginalEntries;
  }
</script>

<main class="container">
  <Navbar sortingIndicator={sortingIndicator} sortingModifier={sortingModifier} on:filter={(event) => filter(event.detail.value)} on:sort={(event) => sort(event.detail.value)}/>
  {#if !entries.length}
    <div id="no-results" class="card">
      <div class="card-body">
        <h5 class="card-title mb-2 text-muted">لا نتائج للبحث ...</h5>
      </div>
    </div>
  {:else}
    <div class="table-responsive">
      <table class="table table-striped table-hover">
        <thead>
          <tr>
            <!-- <th scope="col">#</th> -->
            <th scope="col">
              <a class="text-nowrap text-reset" href="#!" on:click={()=> sort('languages')}>
                <small class="text-muted">{sortingIndicator.languages}</small>
                اللغة
              </a>
            </th>
            <th scope="col">
              <a class="text-nowrap text-reset" href="#!" on:click={()=> sort('titles')}>
                <small class="text-muted">{sortingIndicator.titles}</small>
                العنوان
              </a>
            </th>
            <th scope="col">
              <a class="text-nowrap text-reset" href="#!" on:click={()=> sort('authors')}>
                <small class="text-muted">{sortingIndicator.authors}</small>
                المؤلف
              </a>
            </th>
            <th scope="col">الناشر</th>
            <th scope="col">
              <a class="text-nowrap text-reset" href="#!" on:click={()=> sort('genres')}>
                <small class="text-muted">{sortingIndicator.genres}</small>
                التصنيف
              </a>
            </th>
            <th scope="col">
              <a class="text-nowrap text-reset" href="#!" on:click={()=> sort('pages')}>
                <small class="text-muted">{sortingIndicator.pages}</small>
                الصفحات
              </a>
            </th>
          </tr>
        <tbody>
          {#each entries as entry, idx}
            <tr class:loaned={entry.loaned} class:text-muted={entry.loaned}
            data-bs-html="true" data-bs-toggle={entry.loaned ? 'tooltip' : ''} title={entry.loaned ? '<i class="fa fa-clock-o" aria-hidden="true"></i> هذا الكتاب مُعار' : ''}
            >
              <!-- <th scope="row">{idx+1}</th> -->
              <td>{#if entry.languages}{languageIcon(entry.languages.language)}{/if}</td>
              <td>
                <a href="#" on:click|preventDefault={() => showEntryCard(entry)}>
                  {entry.title || ''}
                </a>
                </td>
              <td>
                {#if entry.authors}
                  {typeof(entry.authors.author) === 'string' ? entry.authors.author : entry.authors.author[0]}
                {/if}
              </td>
              <td>{entry.publisher || ''}</td>
              <td>{#if entry.genres}
                {typeof(entry.genres.genre) === 'string' ? tr(entry.genres.genre) : tr(entry.genres.genre[0])}
                {/if}
              </td>
              <td>{entry.pages || ''}</td>
            </tr>
          {/each}
        </tbody>
      </table>
    </div>
  {/if}
  <Card entry={current} borrower={borrowers.find(x => x.loan['@entryRef'] === entry.id)} />
  <BackToTop />
</main>

<style lang="scss">
  @import 'scss/main';

  main {
    direction: rtl;

    #no-results {
      margin-top: 3.9rem;
    }

    table {
      margin-top: 3.5rem;
      a {
        text-decoration: none;
      }

      tr.loaned {
        text-decoration: underline overline line-through;
      }
    }
  }
</style>
