<script>
export let sortingModifier, sortingIndicator;
import { createEventDispatcher } from 'svelte';
const dispatch = createEventDispatcher();

function filter(event) {
  dispatch('filter', {
    value: event.target.value
  });
}

function sort(type) {
  dispatch('sort', {
    value: type
  });
}

</script>

<nav class="navbar fixed-top navbar-light bg-light">
  <div class="container-fluid">
    <form class="row container-fluid">
      <div class="col-11">
        <div class="input-group">
          <button class="input-group-text"
          on:click|preventDefault={() => document.getElementById('search-input').focus()}
          >
            <i class="fa fa-search"></i>
          </button>
          <input id="search-input" type="search" class="form-control" placeholder="عنوان | مؤلف | لغة | تصنيف | وسم"
          aria-label="search" aria-describedby="search"
          on:input={filter} onfocus="this.setSelectionRange(0, this.value.length)"
          >
        </div>
      </div>
      <div class="dropdown col-1">
        <button class="btn dropdown-toggle" type="button" id="sort-dropdown-menu" data-bs-toggle="dropdown" aria-expanded="false">
          <i class="fa fa-sort-amount-{sortingModifier}" aria-hidden="true"></i>
        </button>
        <ul class="dropdown-menu" aria-labelledby="sort-dropdown-menu">
            <li><a class="dropdown-item" href="#!" on:click={()=> sort('titles')}><small class="text-muted">{sortingIndicator.titles}</small> العناوين</a></li>
            <li><a class="dropdown-item" href="#!" on:click={()=> sort('authors')}><small class="text-muted">{sortingIndicator.authors}</small> المؤلفين</a></li>
            <li><a class="dropdown-item" href="#!" on:click={()=> sort('languages')}><small class="text-muted">{sortingIndicator.languages}</small> اللغة</a></li>
            <!-- TODO -->
            <!-- <div><button class="button" on:click={() => sort('ratings')}>التقييم</button></div> -->
            <li><a class="dropdown-item" href="#!" on:click={()=> sort('pub_years')}><small class="text-muted">{sortingIndicator.pub_years}</small> تاريخ النشر</a></li>
            <li><a class="dropdown-item" href="#!" on:click={()=> sort('genres')}><small class="text-muted">{sortingIndicator.genres}</small> التصنيف</a></li>
            <li><a class="dropdown-item" href="#!" on:click={()=> sort('pages')}><small class="text-muted">{sortingIndicator.pages}</small> الصفحات</a></li>
        </ul>
      </div>
    </form>
  </div>
</nav>
